.main {
	display: block;
	// min-height: 100%
	flex-grow: 1;
	margin: 0;
	position: relative; }

img,
svg {
	display: inline-block;
	max-width: 100%; }

.container {
	width: 100%;
	max-width: 1340px;
	margin: 0 auto;
	padding: 0 20px; }

.content-center {
	text-align: center;
	margin: 0 auto;
	max-width: 775px; }

.content,
.content-center {
	&.big-text {
		p {
			font-size: 20px;
			line-height: 1.5;
			@include r(768) {
				font-size: 16px; } } }
	h2 {
		margin-bottom: 20px; }
	h4 {
		margin-bottom: 20px; }
	h6 {
		margin-bottom: 15px; }
	dl + dl {
		margin-top: 25px; }
	dl {
		font-family: $f-1;
		dt {
			font-size: 20px;
			font-weight: normal;
			font-stretch: normal;
			font-style: norma;
			line-height: normal;
			letter-spacing: 0.27px;
			color: $main;
			@include r(768) {
				font-size: 18px; } }
		dd {
			opacity: 0.75;
			font-size: 15px;
			font-weight: normal;
			font-stretch: normal;
			font-style: normal;
			line-height: 1.2;
			letter-spacing: normal;
			color: $text;
			margin-top: 10px;
			@include r(768) {
				font-size: 14px; } } }
	p {
		font-size: 18px;
		@include r(768) {
			font-size: 16px; } }
	.bttn {
		margin-top: 55px;
		@include r(768) {
			margin-top: 30px; } } }

h1, h2, h3, h4, h5 {
	font-family: "NewzaldWeb-Book", serif; }

h2 {
	font-size: 40px;
	@include r(768) {
		font-size: 30px; } }
h3 {
	font-size: 30px;
	@include r(768) {
		font-size: 24px; } }
h4 {
	font-size: 26px;
	@include r(768) {
		font-size: 21px; } }
h5 {
	font-size: 22px;
	@include r(768) {
		font-size: 20px; } }

h6 {
	font-family: "CalibreWeb-Medium", sans-serif;
	font-size: 16px;
	font-weight: 500;
	font-stretch: normal;
	font-style: normal;
	line-height: normal;
	letter-spacing: 1.5px;
	text-transform: uppercase; }
